
<template>
    <div>
   <div class="my-popups modal right fade show" style="display:block" role="dialog">
    <loader :is-visible="isLoading"></loader>
        <div class="modal d-block " tabindex="-1" role="dialog">
        <div class="modal-dialog doc-setup-slide">
            <div class="modal-content">
                <div class="modal-header mheader-user py-0">
                    <button type="button" class="close" v-on:click="$emit('close')">
                        <i aria-hidden="true" class="fa fa-times"></i>
                    </button>
                    <h4 class="modal-title py-3">{{ $t('Setup Ticketing') }}</h4>
                </div>
                <div class="modal-body bg-white">
                    <section class="container-fluid p-0">
                        <div class="left-menu-tab new_tab">
                            <div class="col-md-12 pb-0">
                                <div class="row">
                                    <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 p-0 float-left">
                                        <div class="row">
                                            <div class="col-8 pr-0 font-weight-medium">{{ $t('CompleteYourProfile') }}</div>
                                            <div class="col-4 pl-0 text-right"><small>
                                                <i class="fa fa-check-circle pr-1" v-bind:class="{'text-success':SetupProgresText=='Completed', 'text-muted':SetupProgresText!=='Completed'}"></i>{{SetupProgresText}}</small>
                                            </div>
                                        </div>
                                        <div class="progress smaller my-2">
                                            <div s class="progress-bar progress-bar-primary progress-bar px-0 text-center memoryused"
                                                id="divConsumendStorage" :class="`${progressBarColor}`" :style="`width: ${SetupProgress}%; font-weight:500; font-size:14px`"
                                                :title="SetupProgress">{{ SetupProgress }}%</div>
                                        </div>
                                        <div class="level_info my-2 d-block float-left w-100" style="height:auto;">
                                            <ul>
                                                <li>
                                                    <b id="divConsumendStorage"><span>Progress {{CurrentTab}} of {{TotalStep}}</span></b>
                                                </li>
                                            </ul>
                                        </div>
                                    </div>
                                </div>  
                            </div>
                             <div class="mt-0" id="divscrollsetuptabs" style="max-height: calc(100vh - 2px);" v-if="CompanyType=='locating'">
                                <ul class="nav nav-pills flex-column left-tab responivecustomtab lefttabiconfix newtab_des">
                                    <li @click="ShowNextTab(1)" class="nav-item FirstScreen" v-if="CompanyType!='nonlocating'">
                                        <a class="nav-link left-menu" v-bind:class="{'active':CurrentTab==1}"  style="cursor: pointer;"><i class="fa fa-check-square-o pr-2 text-muted"></i>{{ $t('Priority') }}</a>
                                    </li>
                                    <li @click="ShowNextTab(2)" class="nav-item ThirdScreen" v-if="CompanyType!='normal'">
                                        <a class="nav-link left-menu" v-bind:class="{'active':CurrentTab==2}"  style="cursor: pointer;"><i class="fa fa-info-circle font-18 pr-2 text-muted"></i>{{ $t('WorkType') }}</a>
                                    </li>
                                    <li @click="ShowNextTab(3)" class="nav-item SecondScreen" v-if="CompanyType!='normal'">
                                        <a class="nav-link left-menu" v-bind:class="{'active':CurrentTab==3}" style="cursor: pointer;"><i class="fa fa-map-marker pr-2 text-muted"></i>{{$t('Resource Type')}}</a>
                                    </li>
                                    <li @click="ShowNextTab(4)" class="nav-item SecondScreen" v-if="CompanyType!='normal'">
                                        <a class="nav-link left-menu" v-bind:class="{'active':CurrentTab==4}" style="cursor: pointer;"><i class="fa fa-users pr-2 text-muted"></i>{{$t('Client')}}</a>
                                    </li>
                                    <li @click="ShowNextTab(5)" class="nav-item SecondScreen" v-if="CompanyType!='normal'">
                                        <a class="nav-link left-menu" v-bind:class="{'active':CurrentTab==5}" style="cursor: pointer;"><i class="fa fa-envelope pr-2 text-muted"></i>{{$t('Account')}}</a>
                                    </li>
                                </ul>
                            </div>
                            <div class="mt-0" id="divscrollsetuptabs" style="max-height: calc(100vh - 2px);" v-if="CompanyType=='nonlocating'">
                                <ul class="nav nav-pills flex-column left-tab responivecustomtab lefttabiconfix newtab_des">
                                    <li @click="ShowNextTab(1)" class="nav-item FirstScreen" v-if="CompanyType!='nonlocating'">
                                        <a class="nav-link left-menu" v-bind:class="{'active':CurrentTab==1}"  style="cursor: pointer;"><i class="fa fa-check-square-o pr-2 text-muted"></i>{{ $t('Priority') }}</a>
                                    </li>
                                    <li @click="ShowNextTab(2)" class="nav-item ThirdScreen" v-if="CompanyType!='normal'">
                                        <a class="nav-link left-menu" v-bind:class="{'active':CurrentTab==2}"  style="cursor: pointer;"><i class="fa fa-info-circle font-18 pr-2 text-muted"></i>{{ $t('WorkType') }}</a>
                                    </li>
                                    <li @click="ShowNextTab(3)" class="nav-item SecondScreen" v-if="CompanyType!='normal'">
                                        <a class="nav-link left-menu" v-bind:class="{'active':CurrentTab==3}" style="cursor: pointer;"><i class="fa fa-map-marker pr-2 text-muted"></i>{{$t('Resource Type')}}</a>
                                    </li>
                                    <li @click="ShowNextTab(4)" class="nav-item SecondScreen" v-if="CompanyType!='normal'">
                                        <a class="nav-link left-menu" v-bind:class="{'active':CurrentTab==4}" style="cursor: pointer;"><i class="fa fa-users pr-2 text-muted"></i>{{$t('Client')}}</a>
                                    </li>
                                    <li @click="ShowNextTab(5)" class="nav-item SecondScreen" v-if="CompanyType!='normal'">
                                        <a class="nav-link left-menu" v-bind:class="{'active':CurrentTab==5}" style="cursor: pointer;"><i class="fa fa-envelope pr-2 text-muted"></i>{{$t('Account')}}</a>
                                    </li>
                                </ul>
                            </div>
                            <div class="mt-0" id="divscrollsetuptabs" style="max-height: calc(100vh - 2px);" v-if="CompanyType=='normal'">
                                <ul class="nav nav-pills flex-column left-tab responivecustomtab lefttabiconfix newtab_des">
                                    <li @click="ShowNextTab(1)" class="nav-item FirstScreen" v-if="CompanyType!='nonlocating'">
                                        <a class="nav-link left-menu" v-bind:class="{'active':CurrentTab==1}"  style="cursor: pointer;"><i class="fa fa-check-square-o pr-2 text-muted"></i>{{ $t('Priority') }}</a>
                                    </li>
                                </ul>
                            </div>
                        </div>
                        <div class="right-content" v-if="CompanyType=='locating'">
                            <SetUpWorkTypeVue :CompanyType="CompanyType" v-if="CurrentTab==2 && CompanyType!='normal'"></SetUpWorkTypeVue>
                            <AddClientVue :CompanyType="CompanyType" v-if="CurrentTab==4 && CompanyType!='normal'"></AddClientVue>
                            <SetUpResourceTypeVue :CompanyType="CompanyType" v-if="CurrentTab==3 && CompanyType!='normal'" ></SetUpResourceTypeVue>
                            <SetUpPriorityVue :CompanyType="CompanyType" v-if="CurrentTab==1 && CompanyType!='nonlocating'"></SetUpPriorityVue>
                            <SetUpAccountVue :CompanyType="CompanyType" v-if="CurrentTab==5 && CompanyType!='normal'"></SetUpAccountVue>
                        </div>
                        <div class="right-content" v-if="CompanyType=='nonlocating'">
                            <SetUpWorkTypeVue :CompanyType="CompanyType" v-if="CurrentTab==1 && CompanyType!='normal'"></SetUpWorkTypeVue>
                            <AddClientVue :CompanyType="CompanyType" v-if="CurrentTab==3 && CompanyType!='normal'"></AddClientVue>
                            <SetUpResourceTypeVue :CompanyType="CompanyType" v-if="CurrentTab==2 && CompanyType!='normal'" ></SetUpResourceTypeVue> 
                            <SetUpAccountVue :CompanyType="CompanyType" v-if="CurrentTab==4 && CompanyType!='normal'"></SetUpAccountVue>
                        </div>
                        <div class="right-content" v-if="CompanyType=='normal'">
                            <SetUpPriorityVue :CompanyType="CompanyType" v-if="CurrentTab==1 && CompanyType!='nonlocating'"></SetUpPriorityVue>
                        </div>
                    </section>
                </div>
            </div>
        </div>
        </div>
    </div>  
</div>
</template>

<script>
import DataService from '../../services/DataService';
import AddClientVue from './AddClient.vue';
import SetUpAccountVue from './Component/SetUpAccount.vue';
import SetUpPriorityVue from './Component/SetUpPriority.vue';
import SetUpResourceTypeVue from './Component/SetUpResourceType.vue';
import SetUpWorkTypeVue from './Component/SetUpWorkType.vue';
export default {
    components:{
        AddClientVue,
        SetUpWorkTypeVue,
        SetUpResourceTypeVue,
        SetUpPriorityVue,
        SetUpAccountVue
    },
    computed:{
         SetupProgresText(){
           return this.SetupProgress==0?'Not Started':(this.SetupProgress==100?'Completed':'In Progress');
         }
    },
    data(){
        return {
            CurrentTab:0,
            SetupProgress:0,
            CompanyType:'normal',
            TotalStep:0,
            IsStorageSoucesCopmleted: true
        }
    },
    created:function(){
        var vm=this;
        vm.CheckLocateOrNonLocate();
        setTimeout(function () {
            vm.GetStorageSetupStatusDetail();
        }, 1000);
    },
    methods:{
        CheckLocateOrNonLocate:function(){
            debugger
            var vm=this;
            DataService.IsExtraMapFeature().then(function (response) {
                vm.CompanyType=response.data;
                if(vm.CompanyType=='locating'){
                    vm.TotalStep=5;
                }
                else if(vm.CompanyType=='nonlocating'){
                    vm.TotalStep=4;
                }
                else{
                    vm.TotalStep=1;
                }
                vm.CurrentTab=1;
            });
        },
        GetStorageSetupStatusDetail: function () {
            debugger
            var vm = this;
            var url=`companyType=${vm.CompanyType}`
            DataService.GetStorageSetupStatusDetail(url).then(function (response) {
                if (response.data != null) {
                    var res = JSON.parse(response.data);
                    vm.SetupProgress = parseInt(res.data[0].TOTAL_PROGRESS);
                    if(vm.SetupProgress==25)
                    {
                        vm.progressBarColor="progress-bar progress-bar-striped bg-danger"
                    }
                    else if(vm.SetupProgress==50){
                        vm.progressBarColor="progress-bar progress-bar-striped bg-warning"
                    }
                    else if(vm.SetupProgress==75){
                        vm.progressBarColor="progress-bar progress-bar-striped bg-info"
                    }
                    else if(vm.SetupProgress==100){
                        vm.progressBarColor="progress-bar progress-bar-striped bg-success"
                    }
                    vm.$parent.SetupProgress = parseInt(res.data[0].TOTAL_PROGRESS);
                    vm.IsStorageManagementsCopmleted = parseInt(res.data[0].STORAGE_MANAGEMENT) == 100 ? true : false;
                    vm.IsUserStoragesCopmleted = parseInt(res.data[0].USER_STORAGE) == 100 ? true : false;
                    vm.IsClientStoragesCopmleted = parseInt(res.data[0].CLIENT_STORAGE) == 100 ? true : false;
                    //vm.IsStorageConfigurationsCopmleted =  parseInt(res.data[0].STORAGE_CONFIGURATION)==100?true:false;
                    vm.IsStorageSoucesCopmleted = parseInt(res.data[0].STORAGE_SOURCE) == 100 ? true : false;
                }
            });
        },
        ShowNextTab: function (tab) {
            var vm= this;
            vm.isLoading=true;
            vm.CurrentTab= tab;
            vm.isLoading=false;
        },
    }
}
</script>
