<template>
    <section class="page-action">
        <div class="container-fluid mb-4 p-0">
            <div class="row">
                <div class="col-md-12 padding-t_8">

                    <div class="theme-primary partition p-actions-expand tablehead">
                        <span class="p-name text-white">{{ $t('Advance Setup') }}</span>  
                        <span class="p-actions float-right">
                                <RouterLink :to="'/Ticketing/Index'" title="" id="backtolist"
                                    class="p-action-btn text-white float-right" :data-original-title="$t('BacktoList')">
                                    <em class="fa fa-angle-double-left pr-2"></em><br />{{ $t('BacktoList') }}
                                </RouterLink>
                        </span>                     
                    </div>
                </div>
            </div>
        </div>
        <div class="row">
           <div class="col-12 col-md-10 mx-auto">
                <div class="row">
                    <div class="col-md-12 col-lg-6">
                        <a onmousedown="return false;" href="javascript:void(0)" class="doc-setup short-menu-item bg-white px-0 py-4 my-2 position-relative"
                            data-toggle-tooltip="tooltip" data-original-title="Work Diary" @click="ShowSetTicketingPopup()">
                            <div class="col-md-1 col-lg-2 col-xl-1 col-sm-1 col-2">
                                <span class="px-2 d-flex">
                                    <i class="fa fa-exclamation-circle blink-icon pl-1" aria-hidden="true" v-show="SetupProgress!==100" style="color: red;"></i>
                                    <i class="fa fa fa-cog clr-4 font-30" aria-hidden="true"></i>
                                </span>    
                            </div>
                            <div class="col-md-9 col-lg-8 col-xl-9 col-sm-9 col-9">
                                <div class="px-0" id="Userlist" @click="ShowSetTicketingPopup()">
                                    <h5 class="card-title font-16 mb-0">{{ $t('Setup Ticketing Module') }}</h5>
                                    <p class="card-text font-12 pr-5">Set up the module in a few simple steps.</p>
                                </div>
                                <div class="progress smaller mr-5 my-2" v-if="windowHeight<=568">
                                    <div s class="progress-bar progress-bar-primary progress-bar px-0 text-center memoryused" id="divConsumendStorage" :class="`${progressColor}`" :style="`width: ${SetupProgress}%; font-weight:500; font-size:14px`" :title="SetupProgress">{{ SetupProgress }}%</div>
                                </div>
                            </div>
                            <div class="col-md-2">
                                    <span class="barprogress">
                                <div class="Task mt-2">
                                    <div class="c100 small" v-bind:class="[progressColor, ('p' + SetupProgress)]">
                                        <span>{{SetupProgress}}%</span>
                                        <div class="slice">
                                            <div class="bar"></div>
                                            <div class="fill"></div>
                                        </div>
                                    </div>
                                     
                                </div>
                             
                            </span>
                            </div>
                            <div class="col-md-2">
                                <span class="barprogress">
                                    <div class="Task mt-2">
                                        <div class="c100 small" v-bind:class="[progressColor, ('p' + SetupProgress)]" v-if="windowHeight>568">
                                            <span>{{SetupProgress}}%</span>
                                            <div class="slice">
                                                <div class="bar"></div>
                                                <div class="fill"></div>
                                            </div>
                                        </div>
                                    </div>
                                </span>
                            </div>
                            <span class="tickarrowposition d-flex align-items-center">
                                <i class="fa fa-angle-right m-auto text-dark font-20" aria-hidden="true"></i>
                            </span>
                        </a>                 
                    </div>
                
                    <div class="col-md-12 col-lg-6">
                        <a onmousedown="return false;" href="javascript:void(0)" class="doc-setup short-menu-item bg-white px-0 py-4 my-2 position-relative"
                        data-toggle-tooltip="tooltip" data-original-title="Work Diary"  @click="showadvancesettingpopup('Configuration')">
                        <div class="col-md-1 col-lg-2 col-xl-1 col-sm-1 col-2">
                            <span class="px-2">
                              <i class="fa fa fa-cog clr-1 font-30" aria-hidden="true"></i>
                            </span> 
                        </div>
                        <div class="col-md-9 col-lg-8 col-xl-9 col-sm-9 col-9" id="Userlist" @click="showadvancesettingpopup('Configuration')">
                           <h5 class="card-title font-16 mb-0">{{$t('Configuration')}}</h5>
                            <p class="card-text font-12 pr-5">Configure the module as per the requirements.</p>
                        </div>

                        <span class="tickarrowposition d-flex align-items-center">
                            <i class="fa fa-angle-right m-auto text-dark font-20" aria-hidden="true"></i>
                        </span>
                        </a>
                    </div>        
                </div>
           </div>
        </div>
        <div class="col-12 text-center float-left">
            <h5 class="my-4">More Settings</h5>
        </div>
        <div class="col-12 col-md-10 m-auto">
          <div class="listing">
                <div class="row">
                    <h6 class="ml-3 mb-0">Ticketing Setting</h6>
                </div>
                <div class="row card-view bg-border custom-box-width">
                    
                    <template v-for="(item) in moreSettingsArray">
                        <div class="col-sm-12 col-lg-3 col-xl-2 col-md-12 div-cardView"  v-if="item.visibleTo.includes(CompanyType)">
                            <div class="box docattachment py-5 my-3 border" role='button' v-on:click="RedirectToLinks(item.name)">
                                <i class="nxt-page-icon fa fa-share-square-o" aria-hidden="true"></i>
                                <div class="box-inner1 d-flex align-items-center m-auto">
                                    <i :class="'fa font-20 m-auto text-white '+ item.icon" aria-hidden="true"></i>
                                </div>
                                <div class="row mx-0 align-items-end">                                
                                    <div class="col-md-12 font-14 mt-2 text-center">{{ item.diaplayName }}</div>
                                </div>
                            </div>
                        </div>
                    </template>
                    
                </div>
            </div>  
        </div>
        <modalAdvanceSettingVue v-if="ShowManageView" :moduleId="moduleId" @close="close"></modalAdvanceSettingVue>
        <SetupTicketingVue v-if="showSetupTicketingPopup" @close="close"></SetupTicketingVue>
    </section>
</template>
<script>
import DataService from '../../services/DataService';
import modalAdvanceSettingVue from './modalAdvanceSetting.vue';
import SetupTicketingVue from './SetupTicketing.vue';
export default {
    components:{
        modalAdvanceSettingVue,
        SetupTicketingVue
    },
    data: function(){
        return {
            ShowManageView:false,
            showSetupTicketingPopup:false,
            SetupProgress:0,
            CompanyType:'normal',
            moduleId:0,
            moreSettingsArray: []
        }
    },
    created: function() {
        var vm=this;
        vm.CheckLocateOrNonLocate();
        setTimeout(function () {
            vm.GetStorageSetupStatusDetail();
        }, 1000);
        this.moreSettingsArray.push({name: 'Canned', diaplayName: this.$t('Canned Reply'), icon: 'fa-envelope', visibleTo: ['normal', 'nonlocating', 'locating']});
        this.moreSettingsArray.push({name: 'FormBuilder', diaplayName: this.$t('FormBuilder'), icon: 'fa-folder-open', visibleTo: ['nonlocating', 'locating']});
        this.moreSettingsArray.push({name: 'IncidentType', diaplayName: this.$t('IncidentType'), icon: 'fa-chain-broken', visibleTo: ['nonlocating', 'locating']});
        this.moreSettingsArray.push({name: 'MapExtent', diaplayName: this.$t('Map Extent'), icon: 'fa-globe', visibleTo: ['nonlocating', 'locating']});
        this.moreSettingsArray.push({name: 'MapConfiguration', diaplayName: this.$t('Map Configuration'), icon: 'fa-map', visibleTo: ['nonlocating', 'locating']});
        this.moreSettingsArray.push({name: 'MapStatus', diaplayName: this.$t('Map Status'), icon: 'fa-ticket', visibleTo: ['nonlocating', 'locating']});
        this.moreSettingsArray.push({name: 'ResourceType', diaplayName: this.$t('Resource Type'), icon: 'fa-list', visibleTo: ['nonlocating', 'locating']});
        this.moreSettingsArray.push({name: 'RuleEngine', diaplayName: this.$t('RuleEngine'), icon: 'fa-cog', visibleTo: ['normal', 'nonlocating', 'locating']});
        this.moreSettingsArray.push({name: 'ServiceCrew', diaplayName: this.$t('ServiceCrew'), icon: 'fa-tags', visibleTo: ['nonlocating', 'locating']});
        this.moreSettingsArray.push({name: 'TicketGroup', diaplayName: this.$t('Ticket Group'), icon: 'fa-map-marker', visibleTo: ['normal', 'nonlocating', 'locating']});
        this.moreSettingsArray.push({name: 'Layout', diaplayName: this.$t('Ticket Layout Settings'), icon: 'fa-ticket', visibleTo: ['normal', 'nonlocating', 'locating']});
        this.moreSettingsArray.push({name: 'Chain', diaplayName: this.$t('Chain'), icon: 'fa-chain', visibleTo: ['normal', 'nonlocating', 'locating']});
        this.moreSettingsArray.push({name: 'WorkType', diaplayName: this.$t('WorkType'), icon: 'fa-info-circle', visibleTo: ['nonlocating', 'locating']});
        this.moreSettingsArray.push({name: 'Zone', diaplayName: this.$t('Zone'), icon: 'fa-map-marker', visibleTo: ['nonlocating', 'locating']});
    },
    methods:{
        ShowSetTicketingPopup: function(){
            var vm = this;
            vm.showSetupTicketingPopup = true;
        },
        CheckLocateOrNonLocate:function(){
                debugger
                var vm=this;
                DataService.IsExtraMapFeature().then(function (response) {
                    vm.CompanyType=response.data;
                });
        },
        GetStorageSetupStatusDetail: function () {
            var vm = this;
            var url=`companyType=${vm.CompanyType}`
            DataService.GetStorageSetupStatusDetail(url).then(function (response) {
                if (response.data != null) {
                    var res = JSON.parse(response.data);
                    vm.SetupProgress = parseInt(res.data[0].TOTAL_PROGRESS);
                    vm.moduleId=parseInt(res.data[0].MODULEID)
                    if(vm.windowHeight<568){
                            if(vm.SetupProgress==25)
                        {
                            vm.progressColor="progress-bar progress-bar-striped bg-danger"
                        }
                        else if(vm.SetupProgress==50){
                            vm.progressColor="progress-bar progress-bar-striped bg-warning"
                        }
                        else if(vm.SetupProgress==75){
                            vm.progressColor="progress-bar progress-bar-striped bg-info"
                        }
                        else if(vm.SetupProgress==100){
                            vm.progressColor="progress-bar progress-bar-striped bg-success"
                        }
                    }
                    else{
                    if(vm.SetupProgress==25){
                        vm.progressColor='red'
                    }
                    else if(vm.SetupProgress==50)
                    {
                        vm.progressColor='yellow'
                    }
                    else if(vm.SetupProgress==75)
                    {
                        vm.progressColor='blue'
                    }
                    else if(vm.SetupProgress==100)
                    {
                        vm.progressColor='green'
                    }
                    }
                }
            });
        },
        showadvancesettingpopup:function(){
            var vm=this;
            vm.ShowManageView = true;
        },
        close:function(){
            var vm=this;
            vm.ShowManageView = false;
            vm.showSetupTicketingPopup=false;
        },
        RedirectToLinks: function(redirectType){
            var envLink=process.env;                
            if(redirectType=="FormBuilder")
            {
                var link=envLink.VUE_APP_CURRENT_SITE_ROOT_URL+'/FormBuilder/Index'
                window.open(link);
                return false
            }
            else if(redirectType=="MapConfiguration")
            {
                var link=envLink.VUE_APP_CURRENT_SITE_ROOT_URL+'/Ticketing/MapConfig'
                window.open(link);
                return false
            }
            else if(redirectType=="MapExtent")
            {
                var link=envLink.VUE_APP_CURRENT_SITE_ROOT_URL+'/Ticketing/MapExtend'
                window.open(link);
                return false
            }
            else if(redirectType=="RuleEngine")
            {
                var link=envLink.VUE_APP_CURRENT_SITE_ROOT_URL+'/RuleEngine/Index'
                window.open(link);
                return false
            }
            else if(redirectType=="ServiceCrew")
            {
                var link=envLink.VUE_APP_CURRENT_SITE_ROOT_URL+'/Ticketing/crew'
                window.open(link);
                return false
            }
            else if(redirectType=="TransformPending")
            {
                var link=envLink.VUE_APP_CURRENT_SITE_ROOT_URL+'/Ticketing/TransformPending'
                window.open(link);
                return false
            }
            else if(redirectType=="WorkType")
            {
                var link=envLink.VUE_APP_CURRENT_SITE_ROOT_URL+'/Ticketing/WorkTypeListing'
                window.open(link);
                return false
            }
            else if(redirectType=="Zone")
            {
                var link=envLink.VUE_APP_CURRENT_SITE_ROOT_URL+'/Ticketing/Zone'
                window.open(link);
                return false
            }
            else if(redirectType=="TicketGroup")
            {
                var link=envLink.VUE_APP_CURRENT_SITE_ROOT_URL+'/Ticketing/Group'
                window.open(link);
                return false
            }
            else if(redirectType=="Chain")
            {
                var link=envLink.VUE_APP_CURRENT_SITE_ROOT_URL+'/Ticketing/Chain'
                window.open(link);
                return false
            }
            else if(redirectType=="Chain")
            {
                var link=envLink.VUE_APP_CURRENT_SITE_ROOT_URL+'/Ticketing/Chain'
                window.open(link);
                return false
            }
            else if(redirectType=="IncidentType")
            {
                var link=envLink.VUE_APP_CURRENT_SITE_ROOT_URL+'/Ticketing/IncidentTypeListing'
                window.open(link);
                return false
            }
            else if(redirectType=="Canned"){
                var link=envLink.VUE_APP_CURRENT_SITE_ROOT_URL+'/ReplyTemplate/Index'
                window.open(link);
                return false
            }
            else if(redirectType=="Layout"){
                var link=envLink.VUE_APP_CURRENT_SITE_ROOT_URL+'/Ticketing/ManageLayout'
                window.open(link);
                return false
            }
            else if(redirectType=="MapStatus"){
                var link=envLink.VUE_APP_CURRENT_SITE_ROOT_URL+'/Ticketing/MapStatus'
                window.open(link);
                return false
            }
            else if(redirectType=="ResourceType"){
                var link=envLink.VUE_APP_CURRENT_SITE_ROOT_URL+'/Ticketing/ResourceType'
                window.open(link);
                return false
            }
            else if(redirectType=="TransformPending"){
                var link=envLink.VUE_APP_CURRENT_SITE_ROOT_URL+'/Ticketing/TransformPending'
                window.open(link);
                return false
            }
        },
    }
}
</script>