<template>
    <div class="">
        <loader :is-visible="isLoading"></loader>
        <div class="col-md-12 p-0">
        <div class="tab-pane call-detail fade p-0 border pb-3 active show">
            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 prof_control">
                <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema"
                v-on:OnSubmit="onSubmit"></dynamic-form>
            </div>
        </div>
    </div>
    </div>
</template>
<script>
import DataServices from "@/services/DataService";
export default {
   
    data() {
        return {
            isLoading: false,
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('Accounty Name'),
                            name: "AccountName",
                            value: "",
                            placeholder: this.$t('Enter Account Name'),
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Country'),
                            name: "Country",
                            value: "",
                            config: {
                                options: []
                            },
                            placeholder: this.$t('SelectStatus'),
                            validationRules: { "required": true, max: 100 },
                        },
                    ],
                }
            ],
            buttons: [
                {
                    type: "button",
                    class: "btn btn-warning float-left",
                    text: "Previous",
                    onClick:this.Previous
                },
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                }
            ],
            ListData: []
        }
    },
    created: async function () {
      var vm=this;
      vm.GetCountryDDLData();
    },
    props:{
        CompanyType:{
            type:String
        }
    },
    methods: {
        GetCountryDDLData:async function(){
            var vm = this;
            vm.FormSchema[0].Data[1].config.options = [];
            var appendingUrl = `id=&&moduleName=${'Ticketing'}&type=${'COUNTRIES'}&search=&refId=`;
            await DataServices.GetDDLDataFSM(appendingUrl).then(function (response) {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        response.data.result.DATA.forEach((item, index) => {
                            vm.FormSchema[0].Data[1].config.options.push({
                                value: item.value,
                                name: item.name
                            })
                        })
                    }
                }
            });
        },
        Previous:function(){
            var vm=this;
            if(vm.CompanyType=='locating'){
                vm.$parent.ShowNextTab(4);
            }else{
                vm.$parent.ShowNextTab(3);
            }
        },
        onSubmit() {
            var vm = this;
            var params = `accountName=${vm.FormSchema[0].Data[0].value}&countryId=${vm.FormSchema[0].Data[1].value}`
            DataServices.AddTicketAccount(params).then((response) => {
                debugger
              if(response.data=="1") {
                vm.ShowAlert("Account Name successfully added.","success");
                vm.$parent.GetStorageSetupStatusDetail();
              }
              else if(response.data=="2"){
                vm.ShowAlert(vm.$t('AccountName') +' '+ vm.$t('AlreadyExist'),"warning");
              }
              else{
                vm.ShowAlert(vm.$t('AddedError'),"failure");
              }
            });
        }
    }
}
</script>