<template>
    <div class="row">
        <loader :is-visible="isLoading"></loader>
        <div class="tab-pane call-detail fade p-0 border pb-3 active show">
            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 prof_control">
                        <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit">
                            <template v-slot:tgslot-AutoCreateService="{ data }">
                                <div class="form-group mb-0">
                                    <label class="switch">
                                        <input class="form-check-input custom-control-input dynamic" type="checkbox"
                                            v-model="data.fieldInfo.value" v-bind:name="'IsLogin'"
                                            v-on:change="checkForAutoCreateEnable(data.fieldInfo.value)"
                                            v-disabled="disabled">
                                        <span class="slider round" style="cursor:default;">
                                            <span class="slider-yes">{{ $t('Yes') }}</span>
                                            <span class="slider-no">{{ $t('No') }}</span>
                                        </span>
                                    </label>
                                </div>
                                <span v-if="AutoServiceDisabled" class="text-danger" style=" font-size: 12px;">{{
                                    $t('AutoServiceDisabled') }}</span>
                            </template>
                        </dynamic-form>
                        </div>
    </div>
    </div>
</template>
<script>
import DataServices from "@/services/DataService";
export default {
    props: {
        WorkTypeId: {
            type: Number,
            required: true
        },
        CompanyType:{
            type: String,
            required: true
        }
    },
    data() {
        return {
            isLoading: false,
            AutoServiceDisabled: true,
            FormSchema: [
                {
                    layoutType: "triple",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('WorkTypeName'),
                            name: "WorkTypeName",
                            value: "",
                            placeholder: this.$t('EnterWorkTypeName'),
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "SlotField",
                            label: this.$t('AutoCreateService'),
                            name: "AutoCreateService",
                            value: "",
                            placeholder: "",
                            config: {
                                options: []
                            },
                            visibility: true,
                            validationRules: "",
                            settings: {
                                isInSlot: true
                            }
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('DurationType'),
                            name: "DurationType",
                            value: "",
                            placeholder: this.$t('SelectDuration'),
                            validationRules: { "required": true, max: 100 },
                            config: {
                                options: [{ value: "HH", name: "Hours" }, { value: "MM", name: "Minutes" }]
                            }
                        },
                        {
                            astype: "NumericField",
                            label: this.$t('EstimateDuration'),
                            name: "EstimateDuration",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "ColorField",
                            label: this.$t('ColorField'),
                            name: "ColorField",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true, max: 100 },
                        },
                    ]
                }
            ],
            buttons: [
                {
                    type: "button",
                    class: "btn btn-warning float-left",
                    text: "Previous",
                    isVisible:this.CompanyType=='nonlocating'?false:true,
                    onClick:this.Previous
                },
                {
                    type: "button",
                    class: "popupgreenbtn btn skip-btn-clr formbtn widthhalf px-4",
                    text: "Skip",
                    isVisible:this.CompanyType=='normal'?false:true,
                    onClick:this.Skip
                },
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save And Next",
                }
            ],
        }
    },
    created: async function () {
        this.FetchData();
    },
    methods: {
        FetchData: function () {
            var vueObj = this;
            var url = `WtId=${vueObj.WorkTypeId}`;
            vm.isLoading=true;
            DataServices.FetchDataByWorktypeId(url).then(
                function (response) {
                    if (response.data != null && response.data != "") {
                        if (response.data.status == "Success") {
                            (vueObj.FormSchema)[0].Data[0].value = response.data.result.data[0].worktype_name;
                            (vueObj.FormSchema)[0].Data[1].value = response.data.result.data[0].description;
                            (vueObj.FormSchema)[0].Data[2].value = response.data.result.data[0].auto_create_sa;
                            (vueObj.FormSchema)[0].Data[3].value = response.data.result.data[0].min_crew_size;
                            (vueObj.FormSchema)[0].Data[4].value = response.data.result.data[0].rec_crew_size;
                            (vueObj.FormSchema)[0].Data[5].value = response.data.result.data[0].duration_type;
                            (vueObj.FormSchema)[0].Data[6].value = response.data.result.data[0].estimate_duration;
                            (vueObj.FormSchema)[0].Data[7].value = response.data.result.data[0].maximum_duration;
                            (vueObj.FormSchema)[0].Data[8].value = response.data.result.data[0].color;
                        }
                        if (response.data.result.data[0].auto_create_sa == true) {
                            vueObj.AutoServiceDisabled = false;
                        }
                        vm.isLoading=false;
                    }
                }
            );
        },
        Skip:function(){
            var vm=this;
            if(vm.CompanyType=='locating'){
                vm.$parent.ShowNextTab(3);
            }else{
                vm.$parent.ShowNextTab(2);
            }
           
        },
        Previous:function(){
            var vm=this;
            vm.$parent.ShowNextTab(1);
        },
        checkForAutoCreateEnable: function (isChecked) {
            var vm = this;
            if (isChecked == true) {
                vm.AutoServiceDisabled = false;
            } else {
                vm.AutoServiceDisabled = true;
            }
        },
        onSubmit(formfields, val) {
            var vm = this;
            var jsonobj = {
                WorkTypeID: vm.WorkTypeId == null ? 0 : vm.WorkTypeId,
                WorktypeName: formfields.WorkTypeName,
                Desc: formfields.DESCRIPTION,
                Auto: formfields.AutoCreateService,
                MinimumCrew: formfields.MinimumCrewSize,
                Crew: formfields.RecommendedCrewSize,
                DurationType: formfields.DurationType,
                Duration: formfields.EstimateDuration,
                MaxDuration: formfields.MaxDuration,
                color: formfields.ColorField,
                due_date_offset: '',
                Exect: '',
                CommissionType: vm.CommissionType,
                CommissionValue: vm.CommissionValue,
                company_id: null,
                status_id: 1001,
                created_by: null,
                modified_by: null,
                modified_at: ''
            };
            var params = JSON.stringify(jsonobj);
            DataServices.SaveWorkType(params).then((response) => {
                if (response.data.status == 'Success') {
                    var rslt = JSON.parse(response.data.result);
                    if (rslt.MSG == 'ALREADY EXISTS') {
                        vm.ShowAlert(vm.$t("AlreadyExists", [vm.$t("WorkType")]), "failure", true, vm.$t("Alert"));
                        // vm.Close();
                    }
                    else {
                        if (vm.WorkTypeId > 0) {
                            vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("WorkType")]), "success", true, vm.$t("Alert"));
                            vm.$parent.GetStorageSetupStatusDetail();
                            vm.Close();
                        }
                        else {
                            vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("WorkType")]), "success", true, vm.$t("Alert"));
                            if(vm.CompanyType=='locating'){
                                vm.$parent.ShowNextTab(3);
                            }else{
                                vm.$parent.ShowNextTab(2);
                            }
                            vm.$parent.GetStorageSetupStatusDetail();
                        }
                        vm.$parent.FetchRecords();
                    }
                }
                else {
                    vm.ShowAlert(vm.$t("AddedError", [vm.$t("Resource")]), "failure", true, vm.$t("Alert"));
                    vm.Close();
                }
            });
        }
    }
}
</script>