<template>
    <section>
        <div class="tab-pane call-detail fade p-0 border pb-3 active show">
            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 prof_control">
        <Form ref="AddClient">
            <dynamic-form lang="en" ref="AddClients" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit"></dynamic-form>
            <div id="accordion" class="col-12 pt-2">
                <div class="card mb-0">
                    <div class="card-header DefaultTab">
                        <a class="card-link collapsed" data-toggle="collapse" href="#collapseone">
                            <div class="conversation-date heading-border"><span>Advanced Settings</span></div>
                        </a>
                    </div>
                    <div id="collapseone" class="collapse show" data-parent="#accordion" style="">
                        <div class="card-body">
                            <dynamic-form lang="en" :buttons="buttons2" :schema="FormSchema2" v-on:OnSubmit="onSubmit"></dynamic-form>
                        </div>
                    </div>
                </div>
            </div>
            
        </Form><br>
        <div class="row flex-row-reverse">
            <div class="col-lg-6 text-right">
                <button type="button" @click="Previous" class="btn btn-warning mr-2">{{$t('Previous')}}</button>
                <button type="button" @click="Skip" class="popupgreenbtn btn skip-btn-clr formbtn widthhalf px-4 mr-2">{{$t('Skip')}}</button>
                <a href="javascript:;" id="btnSave" @click="SaveData" class="btn btn-success savebtn_unique" value="Save" title="" name="btnSave" onclick="openConfirmBoxForMail();" data-toggle-tooltip="tooltip" data-original-title="Save"><i class="fa fa-save pr-2"></i>Save And Next</a>
            </div>
        </div>
        </div>
        </div>
    </section>
</template>
<script>
import DataService from '../../services/DataService';
export default{
   data(){
    return {
        UserType:0,
        FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TextField",
                            label: 'First Name',
                            name: "FirstName",
                            value: "",
                            placeholder: "Enter First Name",
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "TextField",
                            label: 'Last Name',
                            name: "LastName",
                            value: "",
                            placeholder: "Enter Last Name",
                            validationRules: { "required": true,},
                        },
                        {
                            astype: "TextField",
                            label: 'Email',
                            name: "Email",
                            value: "",
                            placeholder: "Enter Last Name",
                            validationRules: { "required": true,},
                        },
                        {
                            astype: "SelectField",
                            label: 'Gender',
                            name: "Gender",
                            value: "",
                            config:{
                                options:[{value:'M',name:'Male'},{value:'F',name:'Female'}]
                            },
                            placeholder: "Select",
                            validationRules: { "required": true,},
                        },
                        {
                            astype: "RadioField",
                            label: 'Account',
                            name: "Gender",
                            value: 1,
                            config:{
                                options:[{value:1,name:'Existing'},{value:2,name:'Create New'}]
                            },
                            placeholder: "Select",
                            validationRules: { "required": false,},
                        },
                        {
                            astype: "SelectField",
                            label: 'Parent Accounts',
                            name: "ParentAccounts",
                            value: "",
                            config:{
                                options:[]
                            },
                            placeholder: "Select",
                            validationRules: { "required": true,},
                        },
                        {
                            astype: "SelectField",
                            label: 'Client Owner',
                            name: "ClientOwner",
                            value: "",
                            config:{
                                options:[]
                            },
                            placeholder: "Select",
                            validationRules: { "required": true,},
                        }
                    ],
                }
        ],
        FormSchema2: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "SelectField",
                            label: 'Time Zone',
                            name: "TimeZone",
                            value: "",
                            config:{
                                options:[]
                            },
                            placeholder: "Select",
                            validationRules: { "required": false,},
                        },
                        {
                            astype: "SelectField",
                            label: 'Date Format',
                            name: "Date",
                            value: 1,
                            config:{
                                options:[{ name: "M/d/yyyy", value:"M/D/YYYY" },{ name:"dd/MM/yyyy", value:"DD/MM/YYYY" }]
                            },
                            placeholder: "Select",
                            validationRules: { "required": false,},
                        },
                        {
                            astype: "SelectField",
                            label: 'Role',
                            name: "Role",
                            value: "",
                            config:{
                                options:[]
                            },
                            placeholder: "Select",
                            validationRules: { "required": true,},
                        }
                    ],
                }
        ],
        buttons2: [
            {
                type: "submit",
                class: "btn btn-success",
                text: "<i class='fa fa-save pr-2'></i>Save",
                isVisible:false
            }
        ],
    }
   },
    props:{
        CompanyType:{
            type:String
        }
    },
   created:function(){
    var vm=this;
    setTimeout(() => {
        vm.GetParentAccount();
    }, 500);
    setTimeout(() => {
        vm.GetClientOwner();
    }, 500);
    setTimeout(() => {
        vm.GetTimeZone();
    }, 500);
    setTimeout(() => {
        vm.GetRole();
    }, 500);
    vm.UserType = vm.GetUserInfo.UserType;
   },
   methods:{
    GetParentAccount:async function(){
        var vm=this;
        var params=`moduleName=Ticketing&type=TALYGEN_ACCOUNT`
        await DataService.GetDDLData(params).then(function (response) {
            response.data.DATA.forEach(function (item,i) {
                vm.FormSchema[0].Data[5].config.options.push({
                    name:item.name,
                    value:item.value
                })
            })
        })
        vm.$refs.AddClients.UpdateKeyValue();
    },
    Skip:function(){
        var vm=this;
        if(vm.CompanyType=='locating'){
            vm.$parent.ShowNextTab(5);
        }else{
            vm.$parent.ShowNextTab(4);
        }
    },
    GetClientOwner:async function(){
        var vm=this;
        var params=`moduleName=Client&type=`
         DataService.GetClientOwner(params).then(function (response) {
            response.data.forEach(function (item,i) {
                vm.FormSchema[0].Data[6].config.options.push({
                    name:item.USERNAME,
                    value:item.USERID
                })
               
            })
            vm.$refs.AddClients.UpdateKeyValue();
        })
    },
    Previous:function(){
        var vm=this;
        if(vm.CompanyType=='locating'){
            vm.$parent.ShowNextTab(3);
        }else{
            vm.$parent.ShowNextTab(2);
        }
    },
    GetTimeZone:async function(){
        var vm=this;
        await DataService.GetTimeZone().then(function (response) {
            response.data.forEach(function (item,i) {
                vm.FormSchema2[0].Data[0].config.options.push({
                    name:item.display_name,
                    value:item.timezone_id 
                })
            });
            vm.$refs.AddClients.UpdateKeyValue();
        })
    },
    GetRole:async function(){
        var vm=this;
        var userTypeId=0;
        if(vm.UserType=="CA"){
            userTypeId=1;
        }
        else if(vm.UserType=="PM"){
            userTypeId=2;
        }
        else if(vm.UserType=="NU"){
            userTypeId=3;
        }
        else if(vm.UserType=="CL"){
            userTypeId=4;
        }
        var params=`moduleName=Ticketing&type=TALYGEN_ROLE&refId=${userTypeId}`
        await DataService.GetDDLData(params).then(function (response) {
            debugger
            response.data.DATA.forEach(function (item,i) {
                vm.FormSchema2[0].Data[2].config.options.push({
                    name:item.value,
                    value:item.name
                })
            })
        })
        vm.$refs.AddClients.UpdateKeyValue();
    },
    SaveData:function(){
        var vm=this;
        vm.$refs.AddClient.validate().then(result=>{
            if(result){
                let addClient={
                    "FirstName":vm.FormSchema[0].Data[0].value,
                    "LastName":vm.FormSchema[0].Data[1].value,
                    "Email":vm.FormSchema[0].Data[2].value,
                    "Gender":vm.FormSchema[0].Data[3].value,
                    "Account":vm.FormSchema[0].Data[4].value,
                    "ParentAccounts":vm.FormSchema[0].Data[5].value,
                    "ClientOwner":vm.FormSchema[0].Data[6].value,
                    "TimeZone":vm.FormSchema2[0].Data[0].value,
                    "DateFormat":vm.FormSchema2[0].Data[1].value,
                    "Role":vm.FormSchema2[0].Data[2].value,
                    "DepartmentId":null,
                    "DesignationId":null,
                    "AccountName":null,
                    "CompanyName":null,
                    "SuperVisorId":null,
                    "UserTypeId":4,
                    "HomeBuisnessUnitId":null,
                    "PayClass":null,
                    "JobType":null,
                    "StartDate":null,
                    "Minitial":null
                }
                DataService.AddClient(addClient).then(function (response) {
                    if(response.data[0].Status=='Success')
                    {
                        vm.ShowAlert(vm.$t('AddedSuccess', [vm.$t('Client')]), "Success", true, vm.$t('Alert'));
                        // vm.ShowAlert(vm.$t('AddClientMsg'),"success");
                        if(vm.CompanyType=='locating'){
                            vm.$parent.ShowNextTab(5);
                        }else{
                            vm.$parent.ShowNextTab(4);
                        }
                        vm.$parent.GetStorageSetupStatusDetail();
                    }
                    else{
                        vm.ShowAlert(vm.$t("ErrorWhileSaving"), "failure", true, vm.$t('Alert'));
                    }
                })
            }
        }) 
    }
   }
}
</script>
